/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import categoriesManagementData from './data-bb-categories-management-http';

const categoriesManagementDataModuleKey = 'data-bb-categories-management-http-ng';

export const categoriesManagementDataKey = 'data-bb-categories-management-http-ng:categoriesManagementData';

export default ng
  .module(categoriesManagementDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(categoriesManagementDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       categoriesManagementData(config),
      ],
    };
  }])
  .name;



